import React, { Component } from 'react';
import Logo from '../img/Logo.png';
import { FormGroup,Form, Input}  from 'reactstrap';
import {Link} from "react-router-dom";


class Library extends Component{
    render(){
      
        return (
            <div>
                <div className="logoImg">
                    <img src={Logo} alt="logo" style={{height: '200px', display: 'inline-block'}}/>
                    <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica'}}>Installation Guide </div>
                </div>
                <div>
                    <Form className="login-form" style={{textAlign:'right'}}>
                        <FormGroup>
                            <Input  type="textarea" rows="20" cols="10" name="text" 
                            value =" Stable — Ubuntu 16.04
                            1) 16 core preferred(Minimum 8 cores)
                            2) 32 gb RAM (Minimum 16 GB)
                            3) 1TB / 500 Gb HDD (As per requirement , 200Gb min.)"/>
                        </FormGroup>
                    </Form>
                </div>            
            <Link to="/ChatBotConfigurationLayout">  
                <h4 className="text-right" style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5'}}>Accept and Proceed</h4>
            </Link>
            <Link to="/ChatBotConfigurationLayout">  
                <h4 className="text-right" style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5'}}>Decline</h4>
            </Link>
        </div>
        );
        }


}

export default Library;