import React, { Component } from 'react';
import Toolbar from '../Toolbar/Toolbar';
import InstallationGuide from '../InstallationGuide/InstallationGuide';


class ChatBotConfigurationLayout extends Component{
    render(){
      
        return (
            <div>
                 <header className="Layout-header"> 
                    <Toolbar/>
                </header>
                <main style={{marginLeft: '136px'}}>
                  <InstallationGuide/>
                </main>
        </div>
        );
        }


}

export default ChatBotConfigurationLayout;