import React, { Component } from 'react';
import './Dashboard.css';
import Logo from '../img/Logo.png';
import NewInstances from './NewInstances/NewInstances';


class Toolbar extends Component{
    render(){
      
        return (
            <div>
            <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '200px', display: 'inline-block'}}/>
                <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica'}}>Dashboard</div>
            </div>
            <br/>
            <div className="tabView" style={{alignContent:'center'}}>
                <div className="tab"><button onClick="">Running Instances</button></div>
                <div className="vl"></div>
                <div className="tab"><button onClick="" >New Instances</button></div>
            </div>
            <div><NewInstances/></div>
            </div>
        );
        }


}

export default Toolbar;