import React, { Component }  from 'react';
import "./Login.css"
import { Form, FormGroup, Label, Input} from 'reactstrap';
import {Redirect} from 'react-router-dom';



export default class Login extends Component{
    constructor(props){
        super(props)
        let loggedIn = false
        this.state = {
            email: '',
            password: '',
            loggedIn
        }

        this.onChange = this.onChange.bind(this)
        this.submitForm = this.submitForm.bind(this)
    }

onChange(e){
    this.setState({
        [e.target.name]: e.target.value
    })
}

submitForm(e){
    e.preventDefault()
    const {email, password} = this.state

    //logic
    if(email === "admin@zensar.com" && password === "admin")
    {
        localStorage.setItem("token", "123456789")
        this.setState({
            loggedIn: true
        })
    }

}
    render(){
        if(this.state.loggedIn){
            return <Redirect to = "/DashboardLayout"/>
        }
        return(
            <div>
                <h1 className="text-center">
                    <span className="font-weight-bold">SAMURAI</span>
                </h1>
                <Form className="login-form" onSubmit= {this.submitForm}>
                
                    <h2 className="text-center"> Login Page </h2>
                    <FormGroup>
                    <Label>Email</Label>
                    <Input type="email" placeholder = "Email" name="email" value={this.state.email} onChange={this.onChange}/>
                    </FormGroup>
                    <FormGroup>
                    <Label>Password</Label>
                    <Input type="password" placeholder = "Password" name="password" value={this.state.password} onChange={this.onChange}/>
                    </FormGroup>
                    <Input type="Submit"/>
                </Form>
            </div>
        )
    }
}