import React, { Component } from 'react';
import './Toolbar.css';
import DashboardImg from '../img/Dashboard.png';
import LibraryImg from '../img/Library.png';
import NotificationImg from '../img/Notification.png';
import UserImg from '../img/User.png';
import LogoutImg from '../img/Icon ionic-ios-exit.svg';
import {Link} from "react-router-dom";


class Toolbar extends Component{
    render(){
      
        return (
            <div>
                <div className="toolbar">
                    <nav className="toolbar_navigation">
                        <div className="toolbar_navigation_items">
                            <ul>
                                <br/>    
                                <Link to="/DashboardLayout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={DashboardImg} alt="Dashboard" />
                                    <p>Dashboard</p>
                                    </h6>
                                </Link>  
                              
                                <Link to="/LibraryLayout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={LibraryImg} alt="Library" />
                                        <p>Library</p>
                                     </h6>
                                </Link>  
                               
                                <Link to="/Layout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={NotificationImg} alt="Notification" />
                                        <p>Notification</p>
                                     </h6>
                                </Link>     
                                
                                <Link to="/Layout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={UserImg} alt="User" />
                                        <p>User</p>
                                     </h6>
                                </Link>   
                               
                                <Link to="/Logout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={LogoutImg} alt="User" />
                                     </h6>
                                </Link>  
                            </ul>
                        </div>
                    </nav>
                </div>
                
            </div>
        );
        }


}

export default Toolbar;