import React from 'react';
import {Switch, Route} from 'react-router-dom';
import DashboardLayout from './Components/DashboardLayout/DashboardLayout';
import Logout from './Components/Logout/Logout';
import Login from './Components/Login/Login';
import LibraryLayout from './Components/LibraryLayout/LibraryLayout';
import ChatBotConfigurationLayout from './Components/ChatBotConfigurationLayout/ChatBotConfigurationLayout';
import RecommConfigurationLayout from './Components/RecommConfigurationLayout/RecommConfiguration';

function App() {
  return (
    <Switch>        
      <Route exact path="/Samurai1.0" component={Login}/>
      <Route path="/Logout" component={Logout}/>
      <Route path="/DashboardLayout" component={DashboardLayout}/>   
      <Route path="/LibraryLayout" component={LibraryLayout}/>   
      <Route path="/ChatBotConfigurationLayout" component={ChatBotConfigurationLayout}/>   
      <Route path="/RecommConfigurationLayout" component={RecommConfigurationLayout}/>         
  </Switch>
  );
}

export default App;
