import React, { Component } from 'react';
import Logo from '../img/Logo.png';
import ZEVA from '../img/ZEVA.png';
import ServiceNow from '../img/ServiceNow.png';
import './Library.css';
import {Link} from "react-router-dom";


class Library extends Component{
    render(){
      
        return (
            <div>
                <div className="logoImg">
                    <img src={Logo} alt="logo" style={{height: '200px', display: 'inline-block'}}/>
                    <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica'}}>Service Library 
                        <p style={{textAlign: 'center',fontSize: '15px', fontFamily:'Helvetica', color:'#2D3642'}}>(Access all your services here)</p>
                    </div>
                </div>
            <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5'}}>Category 1 
                <p style={{textAlign: 'right',fontSize: '15px', fontFamily:'Helvetica', color:'#2D3642'}}>(Click on the service to configure)</p>
            </div>
            <br/>
            <div className="tabView" style={{alignContent:'center'}}>
            <div className="serviceImg" >
                <Link to="/ChatBotConfigurationLayout">  
                    <img src={ZEVA} alt="ZEVA" style={{height: '41px',width:'71px', textAlign:'center', top:'20px'}}/>
                </Link>
            </div>
            <div className="serviceImg">
                <Link to="/RecommConfigurationLayout">  
                    <img src={ServiceNow} alt="ServiceNow" style={{height: '41px',width:'81px', textAlign:'center', top:'20px', paddingTop:'1rem'}}/>
                </Link>
            </div>
            <div className="serviceImg"></div>
            </div>
        </div>
        );
        }


}

export default Library;